import { $$, addClass, addVendorFilesToDOM, createElement } from '../../scripts/__constants';

declare const countUp: any;
declare const Waypoint: any;

const init = (block: Element) => {
  let countUpEls: Element[];
  const countUpArr: any[] = [];
  const allRows = [...block.children];
  const containerEl = createElement('div');
  addClass(containerEl, 'container');

  allRows.forEach((row: HTMLElement, index: number) => {
    addClass(row, 'row');
    containerEl.append(row);
    const allCols = [...row.children];
    allCols.forEach((col: HTMLElement) => {
      addClass(col, `col-12 col-md-${12 / allCols.length}`);
      if (index === 1) {
        addClass(col, 'mt-3 mt-md-5');
      }
      row.append(col);
    });
  });
  if ($$(containerEl, 'h3:first-child').length > 0) {
    $$(containerEl, 'h3:first-child').forEach((h3El: Element) => {
      addClass(h3El, 'mb-0');
    });
  }
  countUpEls = $$(containerEl, 'em');
  countUpEls.forEach((countUpEl) => {
    countUpArr.push({
      el: countUpEl,
      countup: new countUp.CountUp(countUpEl, parseFloat(countUpEl.textContent)),
      animated: false,
    });
  });
  countUpArr.forEach((countupObj: any) => {
    if (!countupObj.animated) {
      new Waypoint({
        element: countupObj.el,
        handler: function () {
          countupObj.countup.start();
          countupObj.animated = true;
        },
        offset: '10%',
      });
    }
  });
  block.append(containerEl);
};

export default async function decorate(block: Element) {
  addVendorFilesToDOM('waypoints', () => {
    addVendorFilesToDOM('countup', () => {
      init(block);
    });
  });
}
